* {
  font-family: Helvetica, sans-serif;
}
.particles {
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 100%;
}
.pp {
  width: 200px;
  height: 200px;
  object-fit: cover;
  object-position: center;
  border-radius: 50%;
  background-color: rgb(99, 99, 99);
}
.photo {
  width: 320px;
  height: 180px;
  object-position: center;
  object-fit: cover;
  margin: 10px 10px 0 10px;
}
.textie {
  font-size: 36px;
}
.icon {
  width: 50px;
  height: 50px;
  margin: 10px;
}
@media only screen and (max-width: 765px) {
  .textie {
    font-size: 18px;
    text-justify: inter-cluster;
  }
  .photo {
    width: 100%;
    height: 300px;
    object-position: center;
    object-fit: cover;
    margin: 10px 0 0 0;
  }
}
.section {
  height: 100vh;
}
